html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

body {
  font-family: 'Open Sans', sans;
}

.container {
  width: 1180px;
  margin: 0 auto;
}

.hero {
  height: 100vh;
  width: 100%;
}

.header {
  padding-top: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
}

.logo img {
  height: 32px;
}

nav ul {
  position: relative;
  top: -3px;
}

nav ul li {
  display: inline-block;
}

nav ul li a {
  font-size: 15px;
  color: #fff;
  text-decoration: none;
  margin-left: 30px;
}

.main {
  margin-top: 127px;
}

.main__title {
  text-align: center;
  color: #FFF;
  font-size: 53px;
  font-weight: 300;
  line-height: 1.2;
  margin-bottom: 42px;
}

.main__subtitle {
  font-size: 18px;
  text-align: center;
  color: #FFF;
  font-weight: 300;
  margin-bottom: 25px;
}

.btn {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  display: inline-block;
  color: #FFF;
  margin: 0 auto;
  font-size: 16px;
  text-decoration: none;
  line-height: 55px;
  padding: 0 30px;
  border-radius: 30px;
  background: #34c482;
}

.features {
  border-top: 8px solid #34c482;
  padding-top: 75px;
  margin-bottom: 80px;
}

.features .container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-left: 30px;
}

.feature:first-of-type {
  margin-left: 0;
}

.feature img {
  margin: 0 auto;
  margin-bottom: 11px;
}

.feature__title {
  font-weight: 400;
  font-size: 20px;
  margin-bottom: 22px;
}

.feature__text {
  font-size: 15px;
  line-height: 1.5;
}
