body {
	font-family:  'Open Sans', sans; }
.container {
	width: 1180px;
	margin: 0 auto; }
.hero {
	height: 100vh;
	width: 100%; }
.header {
	padding-top: 5px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: baseline; }
.logo {
	img {
		height: 32px; } }
nav {
	ul {
		position: relative;
		top: -3px;
		li {
			display: inline-block;
			a {
				font-size: 15px;
				color: #fff;
				text-decoration: none;
				margin-left: 30px; } } } }
.main {
	margin-top: 127px;
	&__title {
		text-align: center;
		color: #FFF;
		font-size: 53px;
		font-weight: 300;
		line-height: 1.2;
		margin-bottom: 42px; }
	&__subtitle {
		font-size: 18px;
		text-align: center;
		color: #FFF;
		font-weight: 300;
		margin-bottom: 25px; } }
.btn {
	position: relative;
	left: 50%;
	transform: translate(-50%, 0);
	display: inline-block;
	color: #FFF;
	margin: 0 auto;
	font-size: 16px;
	text-decoration: none;
	line-height: 55px;
	padding: 0 30px;
	border-radius: 30px;
	background: rgb(52, 196, 130); }

//////////////////////////////////////////////

// FEATURES

//////////////////////////////////////////////
.features {
	border-top: 8px solid rgb(52, 196, 130);
	padding-top: 75px;
	margin-bottom: 80px;
	.container {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
 } }		// width: 110px
.feature {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	margin-left: 30px;
	&:first-of-type {
		margin-left: 0; }
	img {
		margin: 0 auto;
		margin-bottom: 11px; }
	&__title {
		font-weight: 400;
		font-size: 20px;
		margin-bottom: 22px; }
	&__text {
		font-size: 15px;
		line-height: 1.5; } }


